import axios from 'axios';

const service = axios.create({
  baseURL: 'https://api.1mountain.site/api/', // Set the base URL
  timeout: 20000, // Request timeout,
  contentType: 'application/json; charset=utf-8'
});

// Function to retrieve token
const getToken = () => {
  return "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..CwdI2QTjAax-Dwz4oQTLTH_hUErBOw2_1WVS6EJ2MZA";
};

// Request interceptor
service.interceptors.request.use(
  config => {
    // Add the token before the request is sent
    config.headers['Token'] = getToken(); // Add a space after 'Bearer'
    config.headers['yy'] = "d8dea9ba1bd22dcdd215903a5c74a541"; // Add a space after 'Bearer'
    return config;
  },
  error => {
    // Handle request errors
    console.log(error);
    return Promise.reject(error);
  }
);

// Response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data;
    // Handle the response based on your business logic
    return res;
  },
  error => {
    // Handle response errors
    console.log('API Error:', error);
    return Promise.reject(error);
  }
);

export default service;
