<template>
  <div class="video-platform">
    <header>
      <h1>海螺AI 创意视频平台</h1>
      <div class="header-buttons">
        <button class="header-btn">Discord</button>
        <button class="header-btn">创作讨论</button>
      </div>
    </header>

    <main>
      <div class="search-container">
        <input v-model="videoDescription" type="text" placeholder="请描述您想生成的视频内容" />
        <div class="button-container">
          <button class="camera-btn"><i class="fas fa-video"></i></button>
          <button class="generate-btn" @click="generateVideo" :disabled="!videoDescription">生成</button>
        </div>
      </div>

      <div v-if="errorMessage" class="error-message">
        {{ errorMessage }}
      </div>

      <div class="tab-container">
        <div class="tabs">
          <button 
            v-for="tab in tabs" 
            :key="tab.id" 
            :class="['tab', { 'active': activeTab === tab.id }]"
            @click="setActiveTab(tab.id)"
          >
            {{ tab.name }}
          </button>
        </div>
        <div class="tab-slider" :style="{ transform: `translateX(${100 * activeTabIndex}%)` }"></div>
      </div>

      <div class="video-grid">
        <div v-for="video in videos" :key="video.id" class="video-card" :class="{ 'failed': video.status === 5, 'generating': video.status === 1 }">
          <div v-if="video.status === 2" class="video-player">
            <video :src="video.videoURL" controls>
              Your browser does not support the video tag.
            </video>
          </div>
          <p v-if="video.status === 2" class="video-description">{{ video.desc }}</p>
          <div v-if="video.status === 5" class="failed-content">
            <div class="failed-icon">❌</div>
            <p class="failed-text">{{ video.message || '视频内容不符合社区规范，生成失败' }}</p>
          </div>
          <div v-if="video.status === 1" class="generating-content">
            <div class="generating-spinner"></div>
            <p class="generating-text">生成中...{{ video.desc }} {{ video.percent }}%</p>
            <p class="generating-message">{{ video.message }}</p>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from 'vue';
import request from './utils/request';

const tabs = [
  { id: 'featured', name: '精选' },
  { id: 'my', name: '我的 (12)' }
];

const activeTab = ref('featured');
const videos = ref([]);
const currentID = ref(0);
const hasNext = ref(false);
const processing = ref(false);
const videoDescription = ref('');
const generatingVideoId = ref(null);
const errorMessage = ref('');
let statusCheckInterval = null;
let errorMessageTimeout = null;
const statusCheckIntervals = {};  // 用于保存每个视频的定时器
const activeTabIndex = computed(() => {
  return tabs.findIndex(tab => tab.id === activeTab.value);
});

const isGenerating = computed(() => {
  return !!generatingVideoId.value;
});

const setActiveTab = (tabId) => {
  activeTab.value = tabId;
  currentID.value = 0;
  videos.value = [];
  fetchVideos();
};

const getCommonParams = () => {
  return {
    device_platform: 'web',
    app_id: '3001',
    version_code: '22201',
    uuid: 'eb389e4e-5305-4d98-9e97-fc8c1a978266',
    device_id: '299362015985942537',
    os_name: 'Windows',
    browser_name: 'chrome',
    device_memory: '8',
    cpu_core_num: '32',
    browser_language: 'zh-CN',
    browser_platform: 'Win32',
    screen_width: '2560',
    screen_height: '1440',
    unix: '1728231307000'
  };
};

const showErrorMessage = (message) => {
  errorMessage.value = message;
  clearTimeout(errorMessageTimeout);
  errorMessageTimeout = setTimeout(() => {
    errorMessage.value = '';
  }, 1000);
};

const fetchVideos = async () => {
  videos.value = []
  try {
    const response = await request.get('/proxy/v2/api/multimodal/video/my/cursor', {
      params: {
        type: 'next',
        currentID: currentID.value,
        limit: 12,
        scene: 'list',
        ...getCommonParams()
      }
    });
    
    if (response.data && response.data.videos) {
      videos.value = [...videos.value, ...response.data.videos];
      hasNext.value = response.data.hasNext;
      if (response.data.videos.length > 0) {
        currentID.value = response.data.videos[response.data.videos.length - 1].id;
      }
      
      const generatingVideo = response.data.videos.find(video => video.status === 1);
      if (generatingVideo) {
        generatingVideoId.value = generatingVideo.id;
        const generatingVideoIds = response.data.videos
        .filter(video => video.status === 1)
        .map(video => video.id);

        // console.log(generatingVideoIds);

        generatingVideoIds.forEach(i => {
          startStatusCheck(i);
        });
      }
      
    }
  } catch (error) {
    // console.error('获取视频失败:', error);
    showErrorMessage('获取视频失败，请稍后重试');
  } finally {
    processing.value = false;
  }
};

const generateVideo = async () => {
  if (!videoDescription.value) return;
  const info = videoDescription.value
  videoDescription.value = ''

  try {
    const response = await request.post('/proxy/api/multimodal/generate/video', {
      "desc": info,
    }, {
      params: getCommonParams(),
      headers: {
        'Token': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..CwdI2QTjAax-Dwz4oQTLTH_hUErBOw2_1WVS6EJ2MZA',
        'Yy': '619d8762a7c4ae4fe088057ad5d88f33'
      }
    });
    fetchVideos();
    if(response.statusInfo && response.statusInfo.code == 1000060 || 1000061) {
      showErrorMessage(response.statusInfo.message);
      return;
    }

    if (response.data) {
      console.log('视频生成任务已创建:', response.data.id);
      generatingVideoId.value = response.data.id;
      videos.value.unshift({
        id: response.data.id,
        desc: videoDescription.value,
        status: 1,
        percent: 0,
        message: "视频生成中，离开当前界面不影响视频生成"
      });
      // startStatusCheck(generatingVideoId.value);
      
      
    }
  } catch (error) {
    // console.error('生成视频失败:', error);
    if (error.response && error.response.data && error.response.data.statusInfo) {
      // showErrorMessage(error.response.data.statusInfo.message);
    } else {
      // showErrorMessage('生成视频失败，请稍后重试');
    }
  } finally {
    location.reload()
    videoDescription.value = '';
  }
};

const checkVideoStatus = async (id) => {
  try {
    const response = await request.get('/proxy/api/multimodal/video/processing', {
      params: {
        idList: id,
        ...getCommonParams()
      }
    });

    if (response.data && response.data.videos) {
      const videoStatus = response.data.videos[0];
      if (videoStatus) {
        const updatedVideoIndex = videos.value.findIndex(v => v.id === videoStatus.id);
        if (updatedVideoIndex !== -1) {
          videos.value[updatedVideoIndex] = {
            ...videos.value[updatedVideoIndex],
            ...videoStatus,
            percent: videoStatus.percent 
          };
        }

        // 当视频完成或者失败时，停止对应的定时器
        if (videoStatus.status === 2 || videoStatus.status === 5) {
          stopStatusCheck(videoStatus.id); // 停止该视频的定时器
          if (videoStatus.status === 2) {
            generatingVideoId.value = null;
          }
        }
      }
    }

    // 如果没有视频正在处理，停止定时器
    if (!response.data.processing) {
      stopStatusCheck(id);  // 停止该视频的定时器
      generatingVideoId.value = null;
    }
  } catch (error) {
    console.error('检查视频状态失败:', error);
  }
};

const startStatusCheck = (id) => {
  // 如果该视频已经有定时器在运行，跳过
  if (statusCheckIntervals[id]) {
    return;
  }

  statusCheckIntervals[id] = setInterval(() => checkVideoStatus(id), 5000);
};

const stopStatusCheck = (id) => {
  // 清除指定视频的定时器
  if (statusCheckIntervals[id]) {
    clearInterval(statusCheckIntervals[id]);
    delete statusCheckIntervals[id];
  }
};

onMounted(() => {
  videos.value = []
  fetchVideos();
});

onUnmounted(() => {
  clearInterval(statusCheckInterval);
  clearTimeout(errorMessageTimeout);
});
</script>

